.intro {
    @extend .padding-content;
    @include adap(padding-top, 40, 40, 29, 29, 18);
    @include adap(padding-bottom, 135, 135, 135, 150, 171);
    max-width: 1440px;
    margin: 0 auto;
    &__content {
        @include adap(padding-top, 54, 54, 40, 40, 26);
        @include adap(padding-left, 102, 102, 52, 52, 21);
        @include adap(padding-right, 102, 102, 52, 52, 21);
        @include adap(border-radius, 55, 55, 39, 39, 21);
        @include adap(height, 440);
        background-color: var(--green-c);
        position: relative;
        top: 0;
        left: 0;
    }
    &__title {
        @include adap(margin-bottom, 20, 20, 18, 18, 16);
        @include adap(width, 400, 400, 310, 310, 220);
        @include adap(font-size, 65, 65, 48, 48, 32);
        font-family: "MB";
        line-height: 100%;
        text-transform: uppercase;
        color: var(--white-c);
    }
    &__subtitle {        
        @include adap(margin-bottom, 20, 20, 18, 18, 16);
        @include adap(font-size, 25, 25, 20, 20, 16);
        font-family: "MR";
        line-height: 120%;
        color: var(--black-c);
    }
    &__button {
    }
    &__img {
        @include adap(top, 9, 9, 9, 135, 261);
        @include adap(right, -69, -90, -90, -114);
        @include adap(width, 866, 866, 650, 600, 450);
        position: absolute;
        img {
            max-width: 100%;
            width: 100%;
            height: auto;
        }
    }
    &__decor {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        &-img {
            position: absolute;
            transition: transform 0.1s linear;
            img {
                width: 100%;
                height: auto;
            }
        }

        & .intro__decor-img:nth-child(1) {
            top: -121px;
            left: 384px;
            width: 350px;
        }

        & .intro__decor-img:nth-child(2) {
            top: 300px;
            left: -90px;
            width: 250px;
        }

        & .intro__decor-img:nth-child(3) {
            bottom: -60px;
            left: 480px;
            width: 130px;
        }

        & .intro__decor-img:nth-child(4) {
            top: 293px;
            right: -97px;
            width: 300px;
        }

    }
}
